@mixin xs {
  @media (min-width: 375px) {
    @content;
  }
}

@mixin sm {
  @media (min-width: 640px) {
    @content;
  }
}

@mixin md {
  @media (min-width: 768px) {
    @content;
  }
}

@mixin lg {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin xl {
  @media (min-width: 1280px) {
    @content;
  }
}

@mixin xl2 {
  @media (min-width:1400px) {
    @content;
  }
}